import React from 'react';

const ProgressBar = () => {
    return (
        <ul id="progressbar">
			<li className="active">Account Setup</li>
			<li>Social Profiles</li>
			<li>Personal Info</li>
			<li>Personal Details</li>
		</ul>
    );
};

export default ProgressBar;


